import axios from 'axios';
import { useState, useLayoutEffect, useContext, useEffect } from 'react'
import {BrowserRouter as Router, Switch,Route, Redirect} from "react-router-dom";
import Login from "./pages/login/Login";
import Home from "./pages/home/Home";
import VendorPanel from "./pages/vendorPanel/VendorPanel";

import AppLevelContext from "./context/AppLevelContext";
import Footer from './components/footer/Footer';
import Category from './pages/category/Category';
import Product from './pages/product/Product';

axios.defaults.baseURL = 'http://172.20.10.7:8800/api';

function App() {
  const { menuList, setMenuList } = useContext(AppLevelContext)
  
  useEffect(async () => {
        await axios("/category")
            .then((resp) => {
              setMenuList(resp.data)
            })
            .catch((e) => console.log(e))
    }, []);

  return (
    <Router basename='/menu'>
      <Switch>
        <Route exact path="/">
          <Home/>
        </Route>
        <Route path="/category">
          <Category/>
        </Route>
        <Route path="/product">
          <Product/>
        </Route>
      </Switch>
    </Router>
  );
  
}

export default App;
