import { Link } from "react-router-dom";
import "./categoryWidget.css";
import { useState, useLayoutEffect, useContext, useEffect } from 'react'
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight';

function CategoryWidget(props) {
  const [isHovering, setIsHovering] = useState(false);

  const handleMouseEnter = () => {
    setIsHovering(true);
  };

  const handleMouseLeave = () => {
    setIsHovering(false);
  };
  return (
    <div className='categoryWidget'>
      <Link to={`/category/${props.category}`} style={{ textDecoration: 'none', color: isHovering ? "white" : "black", WebkitTextFillColor: isHovering ? "white" : "white"}}  onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
        <img src={props.icon} alt="" className="categoryImg" />
        <span className="categoryTitle">{props.title}</span>
        <div className="categoryAngle" style={{backgroundColor: isHovering ? 'white' : "white"}} onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
        <KeyboardArrowRightIcon style={{color: isHovering ? "white":'black'}} className={'categoryAngleIcon'} onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
        </KeyboardArrowRightIcon>
        </div>
    </Link>

    </div>
  )
}

export default CategoryWidget 